.VacationsStats {
    margin: 2rem 8rem;
    animation: fade-in 0.5s;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.back-vacation-btn {
    padding: 10px 40px;
    border-radius: 5px;
    font-size: 1.1em;
    font-weight: 900;
    background-color: rgb(28, 89, 124);
    color: white;

}

.back-vacation-btn:hover {
    background-color: rgb(16, 53, 74);
    cursor: pointer;


}