.Register {
    display: flex;
    justify-content: center;
    
}

.Register h2 {
    text-align: center;
    font-size: 4em;
}


.Register .inputBox {

    position: relative;
    width: 300px;
    margin: 40px;

}

.Register .inputBox input{

    position: relative;
    width: 100%;
    padding: 30px 10px 10px;
    border: none;
    outline: none;
    border: 0.1px solid grey;
    border-radius: 5px;
    
}

.Register .inputBox .label {
    
    position: absolute;
    color:grey;
    left:0;
    bottom:0;
    padding: 10px 10px;
    transition: 0.5s;
    pointer-events: none;
    font-size:1.3em;
   

}

.Register input:focus ~ .label, .Register input:valid ~ .label {
    padding: 0px 0px 10px 0px;
    background-color: white;
    margin-left:10px;
    transform:  translateY(-39px);
    font-size: 1em;
}

.Register button {
    padding: 10px 40px;
    border-radius: 5px;
    font-size: 1.1em;
    font-weight: 900;
    background-color: rgb(168, 147, 147);
    color:white;

}

.Register button:hover {
    background-color: rgb(117, 102, 102);
    cursor: pointer;
    

}

.Register .span-validation-error {
    color: red;
    font-weight: 500;
    position:absolute;
    width: 200px;
    left:290px;
    top:20px;

}

