.Vacations .vacations-container {

    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
    margin: 5rem;
    margin-bottom: 3rem;
    position: relative;

}

.Vacations .add-btn,
.graph-btn {

    margin-left: 4px;
    padding: 5px 15px;
    border-radius: 5px;
    background-color: #444444;
    color: white;
    font-weight: 900;
    font-size: 1.3em;
    font-family: 'Montserrat', sans-serif;
    cursor: pointer
}

.Vacations .my-vacations-btn {

    position: absolute;
    left: 0px;
    top: 0px;
    margin-top: 10px;
    padding: 5px 15px;
    border-radius: 5px;
    background-color: #444444;
    color: white;
    font-weight: 900;
    font-size: 1.3em;
    font-family: 'Montserrat', sans-serif;
    cursor: pointer;
    margin: 5rem;


}

.Vacations .all-vacations {

    position: absolute;
    left: 0px;
    top: 0px;
    margin-top: 10px;
    padding: 5px 15px;
    border-radius: 5px;
    background-color: white;
    color:  #444444;
    font-weight: 900;
    font-size: 1.3em;
    font-family: 'Montserrat', sans-serif;
    cursor: pointer;
    margin: 5rem;
    margin-left: 18rem;


}

.Vacations h1 {
    font-size: 3.2em;
}