.EditVacation {

    display: flex;
    justify-content: center;

}

.EditVacation h2 {

    text-align: center;
    font-size: 4em;
}


.EditVacation .inputBox {

    position: relative;
    width: 300px;
    margin: 40px;

}

.EditVacation .inputBox input {

    position: relative;
    width: 100%;
    padding: 30px 10px 10px;
    border: none;
    outline: none;
    border: 0.1px solid grey;
    border-radius: 5px;

}

.EditVacation .inputBox .label {

    position: absolute;
    color: grey;
    left: 0;
    bottom: 0;
    padding: 10px 10px;
    transition: 0.5s;
    pointer-events: none;
    font-size: 1.3em;


}

.EditVacation input:focus~.label,
.EditVacation input:valid~.label {
    padding: 0px 0px 10px 0px;
    background-color: white;
    margin-left: 10px;
    transform: translateY(-39px);
    font-size: 1em;
}

.EditVacation .update-vacation-btn {

    padding: 10px 40px;
    border-radius: 5px;
    font-size: 1.1em;
    font-weight: 900;
    background-color: rgb(28, 124, 47);
    color: white;

}

.EditVacation .update-vacation-btn:hover {
    background-color: rgb(16, 72, 27);
    cursor: pointer;


}

.EditVacation .edit-btn-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.EditVacation .cancel-vacation-btn {
    border: none;
    border-bottom: 1px solid;
    background-color: none;
    background-color: white;

}

.EditVacation .cancel-vacation-btn:hover {

    cursor: pointer;
    
}

.EditVacation .span-validation-error {
    color: red;
    font-weight: 500;
    position:absolute;
    width: 200px;
    left:290px;
    top:20px;

}

.EditVacation .edit-image  {
    margin-top:20px;
    width:100%;
    height:100%;
}