.VacationCard {

    height: 350px;
    border-radius: 35px;
    box-shadow: 2px 11px 23px -4px rgba(0, 0, 0, 0.89);
    overflow: hidden;
    position: relative;
    color: #444444;
    transition: 0.4s ease-in-out;

}

.VacationCard > * {

    margin: 0px 20px;

}

.VacationCard .dropdown-menu-btn {

    position: absolute;
    right: 0px;
    bottom: 30px;
    border-radius: 5px;
    padding: 5px 10px;
    border: none;
    color: #444444;
    background: white;
    cursor: pointer;

}

.svg-inline--fa {

    font-size: 1.2em;
    rotate: 0deg;
    transition: 0.35s ease-in-out;

}

.VacationCard .image-container {

    height: 350px;
    position: relative;
    display: flex;
    /* border: 2px solid red; */

}

.VacationCard .image-container:hover .fa-chevron-down {

    animation: MoveUpDown 1s ease-in-out infinite;
    position: relative;
    color: white;
    left: 0;
    bottom: 0;

}

.VacationCard .image-container:hover .dropdown-menu-btn {

    background-color: #444444;

}



.VacationCard img {

    width: auto;
    height: 100%;
    position: absolute;
    left: -100px;
    z-index: -1;

}

.VacationCard .destination {

    font-weight: 900;
    font-size: 1.8em;
    position: absolute;
    left:10px;
    bottom: 20px;
    background-color: white;
    color: rgb(121, 108, 108);
    margin-bottom: 10px;
    padding: 0px 5px;
    border-radius: 5px;

}

.details {

    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 10px;
    margin-bottom: 10px;


}

.VacationCard .description {

    text-align: left;
    line-height: 20px;
    height: 100px;


}

.VacationCard .price {

    color: black;
    font-weight: 700;
    font-size: 1.5em;
    background-color: rgb(49, 105, 114);
    width: fit-content;
    padding: 5px 10px;
    border-radius: 5px;
    margin: 0px auto;

}

p {
    letter-spacing: 0.01em;
    margin: 0px;

}

p::first-letter {
    text-transform: capitalize;
}

.fa-arrow-right {
    font-size: 0.9em;
}

.fa-calendar-days {
    font-size: 1.3em;
}

@keyframes MoveUpDown {

    0%,
    100% {
        bottom: 0;
    }

    50% {
        bottom: 5px;
    }
}


.VacationCard .follow-me-bg {
    z-index: 1;
    position: absolute;
    right: 10px;
    top: 30px;
    border-radius: 50%;
    border: 0px;
    padding: 13px 15px;
    color: transparent;
    background-color: #b2b2b2;
    background-color: rgba(0, 0, 0, .1);
    backdrop-filter: blur(4px);



}

.VacationCard .follow-me-outline {
    z-index: 1;
    position: absolute;
    right: 11px;
    top: 32px;
    border-radius: 50%;
    border: 0px;
    padding: 10px 12px;
    background-color: transparent;
    stroke: black;
    stroke-width: 45;
    font-size: 1.2em;
    stroke: white;
    color: transparent;


}

.followers-counter {
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    left: 10px;
    top: 38px;
    border-radius: 5px;
    border: 2px solid transparent;
    padding: 5px;
    color: white;
    background-color: #b2b2b2;
    background-color: rgba(0, 0, 0, .1);
    backdrop-filter: blur(4px);

}

.followers-counter span {
    color: #444444;
    font-weight: 900;
}

.VacationCard .edit-tools {

    position: absolute;
    padding: 20px;
    right:-20px;
    /* display: flex;
    flex-direction: column;// 
    justify-content: center;
    align-items: center;*/
    display: grid;
    gap:10px; 
    /* border: 2px solid red; */
    align-self: center;
    justify-self: center;
   
    
   

}

.VacationCard .edit-tools > * {

    padding: 8px 10px;
    border-radius: 5px;
    background: #73b1b9;
    border: #0b1e1d 2px solid; 

}

/* Delete button design */
.VacationCard .delete-btn {

    color: rgb(113, 12, 12);
    width:4em;
    height: 3em;
    
}

.VacationCard .delete-btn:hover {

    background: rgb(147, 17, 17);
    color: white;
    cursor: pointer;
    transform: scale(1.1);

}

.VacationCard .delete-btn:hover .fa-trash {
   
    transform: scale(1.2);

}

/* Edit button design */
.VacationCard .inner-edit-btn {
    width:2em;
    height: 1em;
    background-color: #73b1b9;
    border:none;
    
}

.VacationCard .edit-btn:hover .inner-edit-btn {

    color: white;
    background: #042940;
    cursor: pointer;
    transform: scale(1.2); 
    
}

.VacationCard .edit-btn:hover {
    transform: scale(1.2);

}



.VacationCard .inner-edit-btn:hover {
    background: #042940;

}

.VacationCard .edit-btn:hover {

    color: white;
    background: #042940;
    cursor: pointer;
 
}
